.App {
  text-align: center;
}


.App-title {

    text-transform: uppercase;
    font-weight: bold;
}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

section {
  padding-top: 3rem;
  min-height: 80vh;
}
.About {
  min-height: 120vmin;

}

.Contact {

  height: 100vmin;
}
